import React, { Fragment } from "react";
import { Button, ExpansionList, ExpansionPanel } from "react-md";
import Helmet from "react-helmet";
import Card from "react-md/lib/Cards";
import Link from "gatsby-link";
import CardText from "react-md/lib/Cards/CardText";
import PostCover from "../components/PostCover/PostCover";
import PostInfo from "../components/PostInfo/PostInfo";
import SocialLinks from "../components/SocialLinks/SocialLinks";
import PostMasonry from "../components/PostMasonry/PostMasonry";
import PostDescription from "../components/PostDescription/PostDescription";
import SEO from "../components/SEO/SEO";
import config from "../../data/SiteConfig";

import "./post.scss";

export default class PostTemplate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mobile: true
    };
    this.handleResize = this.handleResize.bind(this);
  }
  componentDidMount() {
    this.handleResize();
    window.addEventListener("resize", this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  handleResize() {
    if (window.innerWidth >= 640) {
      this.setState({ mobile: false });
    } else {
      this.setState({ mobile: true });
    }
  }

  render() {
    const { mobile } = this.state;
    const { slug } = this.props.pathContext;
    const postOverlapClass = mobile ? "post-overlap-mobile" : "post-overlap";
    const fileEdges = this.props.data.allFile.edges;
    const postNode = this.props.data.markdownRemark;
    const post = postNode.frontmatter;
    if (!post.id) {
      post.id = slug;
    }
    if (!post.category_id) {
      post.category_id = config.postDefaultCategoryID;
    }
    const coverHeight = mobile ? 180 : 350;
    return (
      <div className="post-page md-grid md-grid--no-spacing">
        <Helmet>
          <title>{`${post.title} | ${config.siteTitle}`}</title>
          <link rel="canonical" href={`${config.siteUrl}${post.id}`} />
        </Helmet>
        <SEO postPath={slug} postNode={postNode} postSEO />
        <PostCover
          postNode={postNode}
          coverHeight={coverHeight}
          coverClassName="md-grid md-cell--12 post-cover"
          fileEdges={fileEdges}
        />
        <div
          className={`md-grid md-cell--12 post-page-contents mobile-fix ${postOverlapClass}`}
        >
          <Card className="md-grid md-cell md-cell--12 card">
            <CardText className="card-body">
              <h1 className="md-display-2 post-header">{post.title}</h1>
              <PostInfo className="box-body" postNode={postNode} />
              <PostDescription className="" description={post.description} />
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 card">
            <CardText className="card-body">
              <div className="post-box">
                {post.lessonSummary &&
                  post.lessonSummary.map(item => (
                    <dl key={item.title}>
                      <dt>{item.title}</dt>
                      <dd>
                        {item.description.split("\n").map(line => {
                          if (line.length > 0) {
                            return <p key={line}>{line}</p>;
                          }
                          return null;
                        })}
                      </dd>
                    </dl>
                  ))}
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 card lessons">
            <CardText className="card-body lessons-body">
              <div className="post-box">
                <h2>커리큘럼</h2>
                <ExpansionList>
                  {post.lessons &&
                    post.lessons.map(lesson => (
                      <div className="temp-block">
                        <div>
                          <h3>{ lesson.title }</h3>
                          {lesson.description.split("\n").map(line => {
                            if (line.length > 0) {
                              return <p key={line}>{line}</p>;
                            }
                            return null;
                          })}
                        </div>
                      </div>
                    ))}
                </ExpansionList>
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 card">
            <CardText className="card-body">
              <div className="post-box">
                <PostMasonry images={post.images} columnCount={post.masonryColumn} />
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 card">
            <CardText className="card-body">
              <div className="post-box">
                <h2>선생님 소개</h2>
                <ul>
                  {post.tutorProfile.length > 0 ? post.tutorProfile.split("\n").map(line => (
                    <li key={line}>
                      {line}
                      <br />
                    </li>
                  )) : '강사 소개가 없습니다.'}
                </ul>
              </div>
            </CardText>
          </Card>
          <Card className="md-grid md-cell md-cell--12 card">
            <CardText className="card-body">
              <div className="post-box">
                <ul>
                  {post.notices &&
                    post.notices.map(notice => <li key={notice}>{notice}</li>)}
                </ul>
              </div>
              <div dangerouslySetInnerHTML={{ __html: postNode.html }} />
            </CardText>
            <div className="post-meta">
              <SocialLinks
                postPath={slug}
                postNode={postNode}
                mobile={this.state.mobile}
              />
            </div>
            <Link className="button-back" to="/">
              <Button floating primary iconClassName="fa fa-arrow-left" />
            </Link>
          </Card>
        </div>
      </div>
    );
  }
}

/* eslint no-undef: "off" */
export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      excerpt
      frontmatter {
        title
        cover
        images
        startAt
        place
        tags
        description
        lessonState
        registerUrl
        lessonPeriod
        lessonSummary {
          title
          description
        }
        lessons {
          title
          description
        }
        notices
        tutorProfile
        masonryColumn {
          count
          lg
          md
          sm
        }
      }
      fields {
        slug
        date
      }
    }
    allFile {
      edges {
        node {
          id
          absolutePath
          childImageSharp {
            id
            resolutions {
              base64
              tracedSVG
              aspectRatio
              width
              height
              src
              srcSet
              srcWebp
              srcSetWebp
              originalName
            }
            internal {
              contentDigest
              type
              owner
            }
            sizes(maxWidth: 1240) {
              ...GatsbyImageSharpSizes
              originalName
            }
          }
        }
      }
    }
  }
`;



