import React, { Fragment } from "react";

const PostDescription = ({ description }) => (
  <div className="post-box">
    {description.split("\n").map(line => (
      <Fragment key={line}>
        {line}
        <br />
      </Fragment>
    ))}
  </div>
);

export default PostDescription;



