import React, { Component } from "react";
import Masonry from "react-masonry-css";
import "./PostMasonry.scss";

class PostMasonry extends Component {
  constructor(props) {
    super(props);
    this.columnCount = this.props.columnCount;
  }
  render() {
    const { count, lg, md, sm } = this.props.columnCount;
    const breakpointColumnsObj = {
      default: count,
      1100: lg,
      700: md,
      400: sm
    };
    return (
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {this.props.images.map(image => (
          <div className="image-wrapper" key={image}>
            <img
              src={`/assets/${image}`}
              alt="클래스 안내 이미지"
            />
          </div>
        ))}
      </Masonry>
    );
  }
}

export default PostMasonry;



